'use client';
import { Button } from "@/components/ui/button";
import { Card } from "@/components/ui/card";
import { Heading } from "@/components/ui/heading";
import { Input } from "@/components/ui/input";
import { Text } from "@/components/ui/text";
import { useState } from 'react';

export default function Login() {
  // 状态管理：记录当前激活的标签页
  const [activeTab, setActiveTab] = useState('login');

  return (
    <div className="flex flex-col items-center justify-center min-h-screen bg-white">
      {/* 顶部标签页容器（添加背景色） */}
      <div className="w-98 max-w-md mb-6 bg-gray-50 rounded-lg p-1">
        <div className="flex">
          {/* 登录按钮（根据状态动态设置样式） */}
          <Button
            variant={activeTab === 'login' ? 'secondary' : 'outline'}
            className={`flex-1 py-2 transition-all duration-200 ${
              activeTab === 'login' ? 'bg-primary text-white' : 'text-gray-600 hover:bg-gray-100'
            }`}
            onClick={() => setActiveTab('login')}
          >
            登录账号
          </Button>
          
          {/* 注册按钮（根据状态动态设置样式） */}
          <Button
            variant={activeTab === 'register' ? 'secondary' : 'outline'}
            className={`flex-1 py-2 transition-all duration-200 ${
              activeTab === 'register' ? 'bg-primary text-white' : 'text-gray-600 hover:bg-gray-100'
            }`}
            onClick={() => setActiveTab('register')}
          >
            注册账号
          </Button>
        </div>
      </div>
      {/* 登录表单 */}
      <Card className="flex flex-col items-start space-y-0 p-4 w-96">
        <Heading as="h2" size="lg">
          登录账号
        </Heading>
        <Text className="text-gray-500 text-sm">
          欢迎回来
        </Text>
        <div className="flex flex-col space-y-4 w-full">
          <label htmlFor="phone" className="text-sm">
            手机号
          </label>
          <Input
            id="phone"
            placeholder="请输入手机号"
            className="w-full"
          />
          <label htmlFor="password" className="text-sm">
            密码
          </label>
          <Input
            id="password"
            placeholder="请输入密码"
            type="password"
            className="w-full"
          />
          <Button variant="outline" className="px-8 py-2 w-full">
            登录
          </Button>
        </div>
      </Card>
    </div>
  );
}